import {
  STAFF_LOGIN_REQUEST,
  STAFF_LOGIN_SUCCESS,
  STAFF_LOGIN_FAIL,
  STAFF_LOGOUT,
  STAFF_DETAILS_REQUEST,
  STAFF_DETAILS_SUCCESS,
  STAFF_DETAILS_FAIL,
  STAFF_DETAILS_RESET,
  STAFF_APPROVE_V_FAIL,
  STAFF_APPROVE_V_REQUEST,
  STAFF_APPROVE_V_RESET,
  STAFF_APPROVE_V_SUCCESS,
  STAFF_DELAY_V_FAIL,
  STAFF_DELAY_V_REQUEST,
  STAFF_DELAY_V_RESET,
  STAFF_DELAY_V_SUCCESS,
  STAFF_REJECT_V_FAIL,
  STAFF_REJECT_V_REQUEST,
  STAFF_REJECT_V_RESET,
  STAFF_REJECT_V_SUCCESS,
  GET_STAFF_FAIL,
  GET_STAFF_REQUEST,
  GET_STAFF_SUCCESS,
  DELETE_STAFF_FAIL,
  DELETE_STAFF_REQUEST,
  DELETE_STAFF_RESET,
  DELETE_STAFF_SUCCESS,
  EDIT_STAFF_FAIL,
  EDIT_STAFF_REQUEST,
  EDIT_STAFF_RESET,
  EDIT_STAFF_SUCCESS,
} from "../constants/staffConstants";

export const staffLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case STAFF_LOGIN_REQUEST:
      return { loading: true };
    case STAFF_LOGIN_SUCCESS:
      return { loading: false, staffInfo: action.payload };
    case STAFF_LOGIN_FAIL:
      return { loading: false, error: action.payload };
    case STAFF_LOGOUT:
      return {};
    default:
      return state;
  }
};

export const staffDetailsReducer = (state = { staff: {} }, action) => {
  switch (action.type) {
    case STAFF_DETAILS_REQUEST:
      return { ...state, loading: true };
    case STAFF_DETAILS_SUCCESS:
      return { loading: false, staff: action.payload.data };
    case STAFF_DETAILS_FAIL:
      return { loading: false, error: action.payload };
    case STAFF_DETAILS_RESET:
      return { staff: {} };
    default:
      return state;
  }
};

