export const getSingleStaffReducer = (state = { staff: {} }, action) => {
  switch (action.type) {
    case GET_STAFF_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case GET_STAFF_SUCCESS:
      return { loading: false, success: true, staff: action.payload.data };
    case GET_STAFF_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
