import React, { useState, useEffect } from "react";
import Navbar from "./Navbar";
import styles from "./styles.module.css";
import {
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
  useToast,
  Input,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { EDIT_ADMIN_RESET } from "../../redux/constants/adminConstants";
import { editAdmins, getAdmins } from "../../redux/actions/adminActions";
import { Link, useParams, useNavigate } from "react-router-dom";

const EditAdmin = () => {
  const toast = useToast();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();
  useEffect(() => {
    dispatch(getAdmins(id));
  }, [dispatch, id]);

  const getAdmin = useSelector((state) => state.getAdmin);
  const { loading: lInfo, error: eInfo, admin } = getAdmin;
  const a = admin && admin.firstname;
  const [firstname, setFirstname] = useState(a);
  const [lastname, setLastname] = useState(admin && admin.lastname);
  const [email, setEmail] = useState(admin && admin.email);
  const [mobile, setMobile] = useState(admin && admin.mobile);

  const submitHandler = (e) => {
    e.preventDefault();
    console.log(firstname, lastname, email, mobile);
    // dispatch(editAdmins(firstname, lastname, email, mobile, id));
  };

  const editAdmin = useSelector((state) => state.editAdmin);
  const { loading, error, success } = editAdmin;

  if (success) {
    toast({
      title: "Notification",
      description: "Admin has been updated",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    // dispatch({ type: EDIT_ADMIN_RESET });
    navigate("/frontdesk/admin");
  }

  return (
    <div>
      <Navbar />
      <div className={styles.contents}>
        <div className={styles.overlay}>
          <small>Add Adminstrator</small>
        </div>

        <div className={styles.guestContainer}>
          <Link
            to="/frontdesk/admin"
            className={`${styles.whiteBtn} ${styles.darkBlue} `}
          >
            Go Back
          </Link>
          <br />
          <br />
          {loading || lInfo ? (
            <Center>
              <CircularProgress isIndeterminate color="green.300" />
            </Center>
          ) : (
            <form onSubmit={submitHandler}>
              {eInfo && (
                <Alert status="error">
                  <AlertIcon />
                  {eInfo}
                </Alert>
              )}
              {error && (
                <Alert status="error">
                  <AlertIcon />
                  {error}
                </Alert>
              )}
              <div className={styles.inputContainer}>
                <label>First Name</label>
                <Input
                  type="text"
                  onChange={(e) => setFirstname(e.target.value)}
                  defaultValue={admin && admin.firstname}
                />
              </div>
              <div className={styles.inputContainer}>
                <label>Last Name</label>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => setLastname(e.target.value)}
                  defaultValue={admin && admin.lastname}
                />
              </div>
              <div className={styles.inputContainer}>
                <label>Email</label>
                <input
                  type="email"
                  className="form-control"
                  onChange={(e) => setEmail(e.target.value)}
                  defaultValue={admin && admin.email}
                />
              </div>
              <div className={styles.inputContainer}>
                <label>Mobile</label>
                <input
                  type="number"
                  className="form-control"
                  onChange={(e) => setMobile(e.target.value.slice(0, 11))}
                  defaultValue={admin && admin.mobile}
                />
              </div>
              <div className={styles.inputContainer}>
                <label style={{ visibilty: "hidden" }}>Submit</label>
                <input
                  type="submit"
                  className="form-control"
                  value="Update Admin"
                />
              </div>
            </form>
          )}
        </div>
      </div>
    </div>
  );
};

export default EditAdmin;
